<template>
  <div class="food-login">
    <img class="login-bg" src="../../assets/img/food-login-bg.jpg" alt>
    <img class="login-img" src="../../assets/img/foodLogo.png" alt>
    <div class="input-container">
      <input type="text" placeholder="酒店代码" v-model="hotelCode">
    </div>
    <div class="input-container">
      <input type="text" placeholder="用 户 名" v-model="userName">
    </div>
    <div class="input-container">
      <div class="pw-btn" @click="toggelPassWordFun()">
        <i v-if="isShowPassWord" class="iconfont icon-eye"></i>
        <i v-else class="iconfont icon-eye1"></i>
      </div>
      <input :type="[isShowPassWord?'text':'password']" placeholder="密 码" v-model="passWord">
    </div>
    <div class="login-btn" @click="loginFun()">登 录</div>
    <div class="settings-container">
      <mt-checklist v-model="checkVal" :options="options"></mt-checklist>
      <div class="settings" @click="gotoSetApi">
        <i class="iconfont icon-shezhi"></i>连接设置
      </div>
    </div>
  </div>
</template>
<script>
import { Indicator, Toast } from "mint-ui";
export default {
  data() {
    return {
      hotelCode: "",
      userName: "",
      passWord: "",
      checkVal: [],
      isShowPassWord: false,
      loginData: null,
      options: [
        {
          label: "记住账号密码",
          value: "1"
        }
      ]
    };
  },
  mounted() {
    if (localStorage.foodUserSet) {
      let foodUserSet = JSON.parse(localStorage.foodUserSet);
      this.hotelCode = foodUserSet.hotelCode;
      this.userName = foodUserSet.userName;
      this.passWord = foodUserSet.passWord;
      this.checkVal = [foodUserSet.checkVal];
    }
  },
  methods: {
    toggelPassWordFun() {
      this.isShowPassWord = !this.isShowPassWord;
    },
    loginFun: function() {
      console.log("checkVal:", this.checkVal);
      if (!this.hotelCode || !this.userName || !this.passWord) {
        this.msg("请输入酒店代码或用户名或密码！");
        return;
      }

      if (this.checkVal[0] == "1") {
        localStorage.foodUserSet = JSON.stringify({
          hotelCode: this.hotelCode,
          userName: this.userName,
          passWord: this.passWord,
          checkVal: 1
        });
      } else {
        localStorage.foodUserSet = "";
      }
      this.$http
        .get("/ohotel/router", {
          params: {
            method: "loginCenter",
            hotelCode: this.hotelCode,
            userName: this.userName,
            passwd: this.passWord
          },
          vv: 5
        })
        .then(res => {
          console.log("denglu:", res);
          if (res.data.code == "0") {
            localStorage.foodUserInfo = JSON.stringify(res.data.data);
            localStorage.waiter = this.userName;
            this.loginData = res.data.data;
            if (window.navigator.userAgent.indexOf("Cordova") > 0) {
              getSn(this.getSnSuccess);
            } else {
              this.checkBinding("111111111", res.data.data);
            }
          } else {
            this.msg(res.data.msg);
          }
        });
    },
    getSnSuccess(v) {
      this.checkBinding(v, this.loginData);
    },
    checkBinding(sn, data) {
      this.$http
        .get("/ohotel/router", {
          params: {
            method: "checkWorkstationExist",
            sinNo: sn
          },
          vv: 5
        })
        .then(res => {
          if (res.data.code == "0") {
            if (res.data.data.existsFlag) {
              data.workStationCode = res.data.data.workStationCode;
              localStorage.foodUserInfo = JSON.stringify(data);
              this.$router.push("/foodIndex");
            } else {
              this.$router.push("/foodSite");
            }
          } else {
            this.msg(res.data.msg);
          }
        });
    },
    gotoSetApi() {
      localStorage.setApi = "foodLogin";
      this.$router.push("/foodSettingApi");
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    }
  },
  watch: {
    hotelCode() {
      this.hotelCode = this.hotelCode.toUpperCase();
    },
    userName() {
      this.userName = this.userName.toUpperCase();
    }
  }
};
</script>
<style lang="less" scoped>
.food-login {
  position: relative;
  .login-bg {
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }
  .login-img {
    width: 106px;
    margin-top: 72px;
    margin-bottom: 1.067rem;
  }
  .input-container {
    position: relative;
    margin: 0 1.2rem;
    border-bottom: 1px solid #ffffff;
    padding: 0.133rem 0;
    margin-bottom: 28px;
    input {
      width: 100%;
      border: none;
      background: none;
      outline: none;
      color: #fff;
      font-size: 0.427rem;
    }

    input::-webkit-input-placeholder {
      color: #fff;
    }
    .pw-btn {
      position: absolute;
      top: 0px;
      right: 0px;
      color: #fff;
    }
  }

  .settings-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 286px;
    margin: auto;
    .settings {
      font-size: 0.373rem;
      color: #ffffff;
      .iconfont {
        margin-right: 6px;
      }
    }
  }
  .login-btn {
    margin: 0 1.2rem;
    margin-top: 0.933rem;
    margin-bottom: 2.4rem;
    font-size: 0.48rem;
    color: #ffffff;
    height: 44px;
    line-height: 44px;
    background-image: linear-gradient(-90deg, #f6a129 0%, #ff6e61 98%);
    border-radius: 2.667rem;
  }
}
</style>
<style lang="less" >
.food-login {
  .mint-field-core {
    text-align: center;
  }
  .settings-container {
    .mint-checklist-title {
      display: none;
    }
    .mint-cell {
      width: auto;
      background: none;
      .mint-cell-wrapper {
        padding: 0;
        background: none;
        .mint-checklist-label {
          padding: 0;
        }
      }
      .mint-checkbox-label {
        color: #fff;
        font-size: 0.373rem;
      }
      .mint-checkbox-core {
        border-radius: 3px;
        width: 0.373rem;
        height: 0.373rem;
        background-color: #fff;
        border-color: #fff;
      }
      .mint-checkbox-core::after {
        border-color: #f89635;
        top: -1px;
        left: 4px;
        width: 6px;
        height: 11px;
      }
    }
  }
}
</style>




